
import teacher from '../assets/home/teacher-square.png';
import { TEXTS } from '../resources/exercises';

function getTextForMode(mode) {
  const levelTexts = TEXTS[mode.selectedLevel] || [];
  return levelTexts.find(text => text.ID === mode.selectedText) || {};
}

function getHomeTextForMode(mode) {
  return getTextForMode(mode).HOME_TEXT;
}

export function getHomeResources(mode) {
  const image = teacher;
  const text = getHomeTextForMode(mode);
  return { image, text };
}

export function getExerciseResources(mode) {
  return getTextForMode(mode).EXERCISES || [];
}

export function getExerciseResource({mode = {}, navigation: { currentView = {} } = {}}) {
  const exercises = getExerciseResources(mode);
  const exerciseObj = exercises.find(({title}) => title === currentView.title);
  return !!exerciseObj ? exerciseObj.exercise : undefined;
}

export function getPasswordImageForMode(mode) {
  return getTextForMode(mode).PASSWORD_IMAGE;
}

export function getMainColourForMode(mode) {
  return getTextForMode(mode).MAIN_COLOUR;
}