
import { getExerciseResources } from './resource-loader';
export function evaluatePassword({password, mode, unlockables = {}}) {
  const newPassword = password.toLowerCase();
  const { password: currentPassword } = unlockables;

  // Work out if this password is a newer one than before.
  const exercises = getExerciseResources(mode);
  const newExerciseIndex = exercises.findIndex(({password = ''}) => password.toLowerCase() === newPassword);
  const currentExerciseIndex = exercises.findIndex(({password = ''}) => password.toLowerCase() === currentPassword);

  return {
    isNew: newExerciseIndex > currentExerciseIndex,
    isUsed: newExerciseIndex !== -1 && newExerciseIndex <= currentExerciseIndex
  };
}

export function getExerciseIndexForPassword({password: latestPassword, exercises}) {
  return exercises.findIndex(({password}) => password.toLowerCase() === latestPassword);
}