import React from 'react';
import { Button, FormControl, Select, Modal, Typography } from '@material-ui/core';
import { withStyles, classNames } from '../utils/styles';
import { LEVELS, TEXTS } from '../resources/exercises';

const width = 400;
const height = 300;

const styles = theme => ({
  modalContainer: {
    backgroundColor: theme.palette.background.paper,
    boxShadow: theme.shadows[5],
    
    position: 'absolute',
    top: '25%',
    left: '50%',
    width,
    height,
    marginLeft: width * -0.5,
    marginTop: height * -0.25
  },
  modalContent: {
    padding: theme.spacing(2, 4, 3)
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120
  },
  settingLabel: {
    width: 120,
    textAlign: 'right',
    margin: 8
  },
  title: {
    marginBottom: 20
  },
  alignRight: {
    justifyContent: 'flex-end'
  },
  // padding: {
  //   height: 80
  // }
});

class Settings extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      pendingLevel: undefined,
      pendingText: undefined
    };
  }

  componentDidMount() {
    const { selectedLevel, selectedText } = this.props;
    if (!selectedLevel || !selectedText) {
      this.props.showChangeModePrompt(true);
    } else {
      this.setState({
        pendingLevel: selectedLevel,
        pendingText: selectedText
      });
    }
  }
  handleLevelChange = event => {
    this.setState({
      pendingLevel: event.target.value,
      pendingText: undefined
    });
  }
  handleTextChange = event => {
    this.setState({pendingText: event.target.value});
  }
  handleSave = () => {
    const { pendingLevel, pendingText } = this.state;
    this.props.setMode({selectedLevel: pendingLevel, selectedText: pendingText});
  }
  handleClose = () => {
    this.props.showChangeModePrompt(false);
  }
  getLevelOptions = () => {
    return LEVELS.map(({NAME, ID}) => ({name: NAME, id: ID}));
  }
  getTextOptions = () => {
    const { pendingLevel } = this.state;
    const texts = TEXTS[pendingLevel] || [];
    return texts.map(({NAME, ID}) => ({name: NAME, id: ID}));
  }
  render() {
    const { classes, showPrompt, selectedLevel, selectedText } = this.props;
    const { pendingLevel, pendingText } = this.state;
    return (
      <Modal
        open={!!showPrompt}
        onClose={this.handleClose}
        disableBackdropClick={true}
        disableEscapeKeyDown={true}
      >
        <div className={classNames(classes.modalContainer, classes.vbox)}>
          <div className={classNames(classes.grow, classes.modalContent)}>
            <Typography variant='h6' color='inherit' noWrap className={classes.title}>Settings</Typography>
            <div className={classes.vbox}>
              <div className={classes.hbox}>
                <Typography variant='h6' color='inherit' noWrap className={classes.settingLabel}>Exam Level</Typography>
                <FormControl className={classes.formControl}>
                  <Select native inputProps={{name: 'mode', id: 'mode-selector'}} className={classes.select} value={pendingLevel} onChange={this.handleLevelChange}>
                    <option value={undefined}></option>
                    { this.getLevelOptions().map(({name, id}) => (<option key={id} value={id}>{name}</option>)) }
                  </Select>
                </FormControl>
              </div>
              <div className={classes.hbox}>
                <Typography variant='h6' color='inherit' noWrap className={classes.settingLabel}>Text</Typography>
                <FormControl className={classes.formControl}>
                  <Select native inputProps={{name: 'mode', id: 'mode-selector'}} className={classes.select} value={pendingText} onChange={this.handleTextChange}>
                    <option value={undefined}></option>
                    { this.getTextOptions().map(({name, id}) => (<option key={id} value={id}>{name}</option>)) }
                  </Select>
                </FormControl>
              </div>
            </div>
          </div>
          <div className={classNames(classes.fixed, classes.hbox, classes.buttonArea)}>
            <Button
              color='primary'
              aria-label='Save'
              onClick={this.handleSave}
              disabled={!pendingLevel || !pendingText}
            >
              Save
            </Button>
            <Button
              color='secondary'
              aria-label='Cancel'
              onClick={this.handleClose}
              disabled={!selectedLevel || !selectedText}
            >
              Cancel
            </Button>
          </div>
        </div>
      </Modal>
          
    )
  }
}

export default withStyles(styles)(Settings);