import React from 'react';

export default class Questions extends React.Component {
  render() {
    const { questions } = this.props;
    return (
      <ol>
        {questions.map((fullQuestion, idx) => {
          if (React.isValidElement(fullQuestion)) {
            return <React.Fragment key={idx}>{fullQuestion}</React.Fragment>;
          }
          const [line, question] = fullQuestion.split(':');
          return (<li key={idx}><strong>{!!question ? `${line}: ` : ''}</strong>{!!question ? question.trim() : fullQuestion}</li>);
        })}
      </ol>
    );
  }
}