import React from 'react';
import { withStyles } from '../../utils/styles';
import { ExpansionPanel, ExpansionPanelDetails, ExpansionPanelSummary, Typography, Link } from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import RemoveIcon from '@material-ui/icons/Remove';
import { getAlias } from '../../utils/hyperlink-aliaser';

const styles = theme => ({
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: '33.33%',
    flexShrink: 0,
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
  }
});

const URL_REGEX = /(https?:\/\/[^\s]+)/g;

class Mission extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      expanded: false
    }
  }
  indexToMissionName = index => {
    const letter = String.fromCharCode(97 + index).toUpperCase();
    return <strong>Mission {letter}</strong>;
  }
  toggleExpanded = () => {
    if (!!this.props.exercise) {
      this.setState({
        expanded: !this.state.expanded
      });
    }
  }
  addHyperlinks(exercise) {
    if (React.isValidElement(exercise)) {
      return exercise;
    }
    try {
      const parts = exercise.split(URL_REGEX);
      const elements = parts.map(part => part.startsWith('http') ? <Link className={this.props.classes.link} key={part} rel='noopener noreferrer' target='_blank' href={part}>{getAlias(part)}</Link> : part);
      // const elements = parts.map(part => part.startsWith('http') ? <a key={part} rel='noopener noreferrer' target='_blank' href={part}>{getAlias(part)}</a> : part);
      return <React.Fragment>{elements}</React.Fragment>;
    } catch (err) {
      return exercise;
    }
  }
  render() {
    const { index, points, description, exercise, compulsory, classes } = this.props;
    const { expanded } = this.state;
    return (
      <ExpansionPanel expanded={expanded} onChange={this.toggleExpanded}>
          <ExpansionPanelSummary expandIcon={!!exercise ? <ExpandMoreIcon /> : <RemoveIcon />}>
            <Typography className={classes.heading}>{this.indexToMissionName(index)} {!!points ? `(${points} Nectar Points)` : undefined }</Typography>
            {!!compulsory ? <Typography className={classes.compulsory}>(Compulsory)&nbsp;</Typography> : undefined}
            {React.isValidElement(description) ? description : <Typography className={classes.secondaryHeading}>{description}</Typography>}
          </ExpansionPanelSummary>
          <ExpansionPanelDetails>
            <Typography color='textPrimary'>
            {this.addHyperlinks(exercise)}
            </Typography>
          </ExpansionPanelDetails>
        </ExpansionPanel>
    );
  }
}

export default withStyles(styles)(Mission);