import { BehaviorSubject, Subject, merge } from 'rxjs';
import { skip, withLatestFrom } from 'rxjs/operators';
import { getState } from '../utils/state-api';
import { TEXTS } from '../resources/exercises';

const STORE_KEY = 'classics';

const defaultState = {};

// TODO Add way to clear progress for all, and for current mode
// localStorage.removeItem(STORE_KEY);

function loadPersistedState() {
  try {
    const cached = localStorage.getItem(STORE_KEY);
    return !!cached ? JSON.parse(cached) : defaultState;
  } catch (e) {
    console.log('Error getting internal state, returning default');
    return defaultState;
  }
}

function loadState() {
  const state = loadPersistedState();
  Object.keys(TEXTS).forEach(level => {
    if (!state[level]) {
      state[level] = {};
      const texts = TEXTS[level];
      texts.forEach(({ID}) => {
        if (!state[level][ID]) {
          state[level][ID] = {};
        }
      })
    }
  });
  return state;
}

const storeSubject = new BehaviorSubject(loadState());
export const store$ = storeSubject.asObservable();

store$.pipe(
  skip(1)
).subscribe(state => {
  // console.log(state);
  localStorage.setItem(STORE_KEY, JSON.stringify(state));
});

const commandSubject = new Subject();
const command$ = commandSubject.asObservable();
export const applyCommand = command => commandSubject.next(command);
export const applyState = newState => commandSubject.next(state => ({...state, ...newState}));

const changeModeSubject = new Subject();
const changeMode$ = changeModeSubject.asObservable();
export const changeMode = mode => changeModeSubject.next(state => ({...state, ...mode}));


merge(
  changeMode$.pipe(
    withLatestFrom(store$, (command, store) => {
      const { mode = {} } = store;
      const newMode = command(mode);
      return {...store, mode: newMode};
    })
  ),
  command$.pipe(
    withLatestFrom(store$, (command, store) => {
      const localState = getState(store);
      const newLocalState = command(localState);

      const { mode } = store;
      const { selectedLevel, selectedText } = mode;

      store[selectedLevel][selectedText] = newLocalState;

      return { ...store };
    }),
  )
)
.subscribe(newState => storeSubject.next(newState));