import React from 'react';
import { withStyles } from '../../utils/styles';
import Mission from './Mission';

class Missions extends React.Component {
  render() {
    const { classes, missions } = this.props;
    return (
      <div className={classes.grow}>
        {missions.map(({description, points, exercise, compulsory}, index) => <Mission key={index} points={points} description={description} exercise={exercise} compulsory={compulsory} index={index} />)}
      </div>
    );
  }
}

export default withStyles()(Missions);