import React from 'react';
import PropTypes from 'prop-types';
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core';
import { CssBaseline } from '@material-ui/core';
import { Toolbar, Drawer, Content, Settings } from './components';
import { createStateApi, getState } from './utils/state-api';
import { withStyles, classNames } from './utils/styles';
import { getMainColourForMode } from './utils/resource-loader';

class App extends React.Component {
  static propTypes = {
    classes: PropTypes.object.isRequired,
    store$: PropTypes.object.isRequired,
    applyCommand: PropTypes.func.isRequired
  };
  constructor(props) {
    super(props);
    this.state = {};
    this.subscription = undefined;
  }
  componentDidMount() {
    this.subscription = this.props.store$.subscribe(state => {
      this.setState({...state, ready: true});
    });
  }
  componentWillUnmount() {
    if (!!this.subscription) {
      this.subscription.unsubscribe();
      this.subscription = undefined;
    }
  }

  render() {
    if (!this.state.ready) {
      return <div></div>;
    }
    const { classes, applyCommand, applyState, changeMode } = this.props;
    const { navigation = {}, unlockables = {}, mode = {} } = getState(this.state);

    const api = createStateApi({state: this.state, applyCommand, applyState, changeMode});

    const {
      navigateHome,
      showChangeModePrompt,
      navigateShop,
      navigatePassword,
      navigateExercise,
      setPassword,
      setMode,
      toggleTheme
    } = api;

    const theme = {
      palette: {
        type: mode.theme || 'light',
        primary: {
          main: getMainColourForMode(mode) || '#34495e'
        },
        secondary: {
          main: '#e74c3c'
        },
        contrastThreshold: 3,
        tonalOffset: 0.1
      }
    };

    return (
      <MuiThemeProvider theme={createMuiTheme(theme)}>
        <CssBaseline />
        <Toolbar className={classes.fixed} mode={mode} navigation={navigation} navigateHome={navigateHome} showChangeModePrompt={showChangeModePrompt} toggleTheme={toggleTheme} />
        <div className={classNames(classes.hbox, classes.noOverflow)}>
          <Drawer unlockables={unlockables} mode={mode} navigateShop={navigateShop} navigatePassword={navigatePassword} navigateExercise={navigateExercise} />
          <Content navigation={navigation} mode={mode} setPassword={setPassword} />
        </div>
        <Settings {...mode} setMode={setMode} showChangeModePrompt={showChangeModePrompt} />
      </MuiThemeProvider>
    );
  }
}

export default withStyles()(App);
