const ALIASES = {
  'quizlet.com': 'Quizlet',
  'memrise.com': 'Memrise',
  'storyboardthat.com': 'Storyboard That',
  'goconqr.com': 'Goconqr',
  'kami.app': 'Kami',
  'drive.google.com': 'GoogleDrive'
}

export function getAlias(text) {
  const match = Object.keys(ALIASES).find(matchText => text.includes(matchText));
  if (!!match) {
    return ALIASES[match];
  }
  return text;
}