import React from 'react';
import { withStyles, classNames } from '../utils/styles';
import { getView } from '../views';

const styles = theme => ({
  root: {
    margin: 16
  }
})

class Content extends React.Component {
  render() {
    const { classes, setPassword, navigation = {}, mode = {} } = this.props;
    const View = getView(navigation.currentView);
    return (
      <div className={classNames(classes.hbox, classes.root, classes.scroll)}>
        <View setPassword={setPassword} mode={mode} navigation={navigation} />
      </div>
    );
  }
}

export default withStyles(styles)(Content);