
import { evaluatePassword } from './password';
import { IDS } from '../views';
const { HOME_ID, SHOP_ID, PASSWORD_ID, EXERCISE_ID } = IDS;

export function getState(state) {
  const { mode = {} } = state;
  const { selectedLevel, selectedText, showPrompt, theme } = mode;
  const { [selectedLevel]: { [selectedText]: localState = {}} = {} } = state;
  const { navigation = {}, unlockables = {} } = localState;
  return { navigation, unlockables, mode: { selectedLevel, selectedText, showPrompt, theme } };
}

export function createStateApi({state, applyCommand, applyState, changeMode}) {
  const { navigation = {}, unlockables = {}, mode = {} } = getState(state);

  const navigateChange = nextView => ({...navigation, currentView: nextView});
  
  const navigate = nextView => {
    const navigation = navigateChange(nextView);
    applyState({navigation});
  }

  const navigateExercise = title => navigate({...EXERCISE_ID, title});
  const navigateHome = () => navigate(HOME_ID);
  const navigateShop = () => navigate(SHOP_ID);
  const navigatePassword = () => navigate(PASSWORD_ID);


  const passwordChange = password => ({...unlockables, password: password.toLowerCase()});
  const setPassword = password => {
    const { isNew, isUsed } = evaluatePassword({password, mode, unlockables});

    if (isNew) {
      const unlockables = passwordChange(password);
      applyState({unlockables});
      alert('You have unlocked a new exercise!');
    } else if (isUsed) {
      alert('You have already unlocked this exercise.');
    } else {
      alert('Sorry, I did not recognise that prompt.');
    }
  }

  const setMode = ({selectedLevel, selectedText}) => {
    changeMode({selectedLevel, selectedText, showPrompt: false});
    navigate(HOME_ID);
  }

  const showChangeModePrompt = open => {
    changeMode({showPrompt: !!open});
  }

  const toggleTheme = () => {
    changeMode({theme: mode.theme === 'dark' ? 'light': 'dark'});
  }

  return {
    navigateHome,
    navigateShop,
    navigatePassword,
    navigateExercise,
    setPassword,
    setMode,
    showChangeModePrompt,
    toggleTheme
    // unlockShop
  };
}