import { LEVEL as ALEVEL, TEXTS as ALEVELTEXTS } from './alevel';
import { LEVEL as GCSE, TEXTS as GCSETEXTS } from './gcse';

export const LEVELS = [
  GCSE,
  ALEVEL
];

export const TEXTS = {
  [ALEVEL.ID]: ALEVELTEXTS,
  [GCSE.ID]: GCSETEXTS
};

export function getTitleForLevel(level) {
  const LEVEL = LEVELS.find(({ID}) => ID === level);
  return !!LEVEL ? LEVEL.NAME : level;
}

export function getTitleForText({text, level}) {
  const texts = TEXTS[level] || [];
  const TEXT = texts.find(({ID}) => ID === text)
  return !!TEXT ? TEXT.NAME : text;
}