import React from 'react';
import { Button, Paper, TextField, Typography } from '@material-ui/core';
import { withStyles, classNames } from '../utils/styles';
import { getPasswordImageForMode } from '../utils/resource-loader';
export const ID = {id: 'PASSWORD', title: 'Enter Prompt'};

const styles = theme => ({
  root: {
    margin: 40
  },
  button: {
    // margin: theme.spacing(1),
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
  },
  paperContainer: {
    minWidth: 300,
    maxWidth: 300,
    height: 200,
    paddingTop: 20
    // padding: 20
  },
  image: {
    height: 200
  }
});

class Password extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      password: ''
    };
  }
  handleChange = event => {
    this.setState({password: event.target.value});
  }
  submitPassword = () => {
    const { setPassword } = this.props;
    setPassword(this.state.password);
    this.setState({password: ''});
  }
  render() {
    const { classes, mode } = this.props;

    return (
    <div className={classNames(classes.root, classes.hbox)}>
      <Paper className={classNames(classes.paperContainer, classes.vbox)}>
        <div className={classNames(classes.vbox, classes.centered)}>
          <Typography variant='h6'>Please sign here</Typography>

          <TextField
          id='password'
          label='Prompt'
          type='password'
          className={classes.textField}
          value={this.state.password}
          onChange={this.handleChange}
          onKeyPress={ev => {
            if (ev.key === 'Enter') {
              this.submitPassword();
            }
          }}
          margin='normal'
          variant='outlined'
          />
          
        </div>
        <div className={classNames(classes.fixed, classes.hbox, classes.buttonArea)}>
          <Button color='primary' className={classes.button} onClick={this.submitPassword} disabled={!this.state.password} value='submit'>
            Submit
          </Button>
        </div>
      </Paper>
      <img src={getPasswordImageForMode(mode)} alt='delivery' className={classes.image} />
    </div>);
  }
}

export default withStyles(styles)(Password);